<template>
  <div class="container outer">
    <div class="row">
      <div class="text-center col-md-6">
        <div class="card-body my-card">
          <div class="text-left">
            <h1><span class="subtitle"></span></h1>
          </div>
          <hr-bar></hr-bar>
          <img class="card-img-top logo" src="../../assets/logo.jpg" alt="Card image cap"/>
          <div class="my-container">
            <ul class="my-text">
              <li></li>
              <li>IT<a href="#">nju-itxia</a><br/><a target="_blank" href="https://itxia.club"></a>
              </li>
            </ul>
          </div>
          <hr-bar></hr-bar>
          <div>
            <div class="input-group mb-3 my-form">
              <label>
                <text-input placeholder="" :text="phone" @update-text="updatePhone"></text-input>
              </label>
              <div class="btn-div">
                <basic-button class="my-button" :text="message" :func="sendCode" :disabled="isDisable"></basic-button>
              </div>
            </div>
            <div class="input-group mb-3 my-form">
              <label>
                <text-input placeholder="" :text="code" @update-text="updateCode"></text-input>
              </label>
              <basic-button class="my-button" text="" :func="startAppointment"></basic-button>
            </div>
          </div>
        </div>
      </div>
      <div class="text-center col-md-6">
        <div class="card-body my-card">
          <!--<img class="card-img-top logo" src="../../assets/logo.jpg" alt="Card image cap"/>-->
          <div class="text-left">
            <h1><span class="subtitle"></span></h1>
          </div>
          <hr-bar></hr-bar>
          <div>
            <div class="my-text">
              <p style="color: darkgoldenrod">-01-018</p>
              <p style="color: #57a94e">-12-25P</p>
              <p style="color: darkgoldenrod">-12-25</p>
              <p style="color: #57a94e;">
                -12-24IT<strong></strong>
                <strong></strong>ITITITIT
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import TextInput from "../../components/TextInput";
  import BasicButton from "../../components/BasicButton";
  import HrBar from "../../components/HrBar";
  import {GET_URL} from "../../utils/value-consts";

  export default {
    name: "UserLogin",
    methods: {
      sendCode: function () {
        this.time = 60;
        this.timer();
        this.isDisable = true;
        let params = new URLSearchParams();
        params.append('phone', this.phone);
        this.$axios.post(
          GET_URL(`/customer/login/code`),
          params,
          {headers: {'Content-Type': 'application/x-www-form-urlencoded'}}
        )
      },
      timer: function () {
        if (this.time > 0) {
          this.message = this.time + 's';
          this.time--;
          setTimeout(this.timer, 1000);
        } else {
          this.message = '';
          this.isDisable = false;
        }
      },
      startAppointment: function () {
        console.log(this.phone);
        console.log(this.code);
        this.$axios.post(
          GET_URL(`/customer/verify`),
          JSON.stringify({
            phone: this.phone,
            code: this.code
          })
        ).then((res) => {
          if (res.data.success) {
            this.$router.push('index');
          } else {
            alert('');
          }
        });
      },
      updatePhone(val) {
        this.phone = val;
      },
      updateCode(val) {
        this.code = val;
      }
    },
    data() {
      return {
        message: '',
        time: 0,
        isDisable: false,
        phone: null,
        code: null,
      }
    },
    components: {
      TextInput,
      BasicButton,
      HrBar
    }
  }

</script>

<style scoped>
  .outer {
    padding-top: 20px;
  }

  .my-card {
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.12),
    0 3px 1px -2px rgba(0, 0, 0, 0.06),
    0 1px 5px 0 rgba(0, 0, 0, 0.12),
    0 -1px 0.5px 0 rgba(0, 0, 0, 0.09);
    background-color: #ffffff;
    margin-bottom: 20px;
  }


  .logo {
    width: 200px;
    margin-bottom: 10px;
  }

  .my-form {
    display: flex;
    justify-content: center;
  }

  .my-input {
    max-width: 200px;
    border-top-right-radius: 0.25em !important;
    border-bottom-right-radius: 0.25em !important;
  }

  .my-text {
    color: #5e5e5e;
    margin-top: 20px;
    margin-bottom: 20px;
    text-align: left;
  }


  .my-text p {
    text-indent: -1rem;
    margin-left: 1rem;
    padding-left: 5px;
    padding-right: 5px;
  }

  .my-container {
    display: flex;
    justify-content: center;
  }

  .subtitle {
    margin-left: 10px;
    font-size: 0.5em !important;
    color: #6f6f6f;
  }

  h1 {
    font-size: 2em;
    margin: 12px 0 0 10px;
    padding-bottom: 3px;
    color: #5e5e5e;
    font-family: 'Lato', "PingFang SC", "Microsoft YaHei", sans-serif;
  }

  li {
    margin-bottom: 3px;
  }

  .btn-div {
    display: flex;
  }

</style>



